*{
    margin:0;
    padding:0;
}
body{
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    background-color: #111;
}
.banner{
    background-size: cover;
    height: 500px;
    color: #fff;
}
.content{
    padding-top: 140px;
    height: 190px;
    padding-left: 20px;
    padding-top: 14%;
}
.title{
    font-size :3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}
.button{
    color:#fff;
    outline: none;
    border:none;
    font-weight: 700;
    border-radius: 5px;
    padding-left:2rem;
    padding-right: 2rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background-color: rgba(51, 51, 51, .5);
    cursor:pointer;
    margin-right: 1rem;
}
button:hover{
    color: black;
    background-color: #e6e6e6;
}
.description{
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 1rem;
    height: 80px;
    max-width: 700px;
}
.fade_bottom{
    height: 10.8rem;
    background-image: linear-gradient(180deg,transparent,rgba(37,37,37,.61),#111);
}
h5{
    
    padding-top: 0.5rem;
    
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}


.row{
    margin-left: 20px;
    color:#fff;
}
.posters{
    display: flex;
    padding: 20px;
    overflow-x: scroll;
    overflow-y: none;
    color: black;
}
posters::-webkit-scrollbar{
    display: none;
}
.posters::-webkit-scrollbar {
    width: 5px;
}
  
  /* Track */
.posters::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px grey; 
    border-radius: 10px;
}
   
  /* Handle */
.posters::-webkit-scrollbar-thumb {
    background: red; 
    border-radius: 10px;
  }
  
  /* Handle on hover */
  .posters::-webkit-scrollbar-thumb:hover {
    background: #b30000; 
  }
.poster{
    cursor: pointer;
    max-height: 300px;
    margin-right: 10px;
    
}
.smallPoster{
    cursor: pointer;
    max-height: 200px;
    margin-right: 10px;
    
}
.subTitle{
    padding-top: 5px;
    color: rgb(255, 255, 255);
}
.smallPoster:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    transition:  2s;
}
.poster:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    transition:  2s;
}
